import React, {PureComponent} from 'react'
import {
  Row,
  Col,
  Table,
  Button,
  message,
  Spin
} from 'antd'
import {connect} from 'react-redux'
import {mapDispatchToProps} from '../redux/mapDispatchToProps'
import {hasVariant, getVariant, removeVariant} from 'Variant'
import JsBarcode from 'jsbarcode'
const ORDER_LIST = 'wms/businessExceedsLOPrint'

@connect(
  state => ({...state.businessExceedsLOPrint}),
  mapDispatchToProps
)
export default class app extends PureComponent {
  constructor() {
    super()
    this.state = {
    }
    this.columns = [{
      title: '',
      dataIndex: 'index',
      key: 'index',
      align: 'center',
      render: (text, record, index) => index + 1
    }, {
      title: '',
      dataIndex: 'productName',
      key: 'productName',
      align: 'center'
    }, {
      title: '',
      dataIndex: 'productCode',
      key: 'productCode',
      align: 'center'
    }, {
      title: '',
      dataIndex: 'productBarCode',
      key: 'productBarCode',
      align: 'center'
    }, {
      title: '',
      dataIndex: 'price',
      key: 'price',
      align: 'center'
    }, {
      title: '',
      dataIndex: 'unit',
      key: 'unit',
      align: 'center'
    }, {
      title: '',
      dataIndex: 'shippedQty',
      key: 'shippedQty',
      align: 'center'
    }, {
      title: '',
      dataIndex: 'amount',
      key: 'amount',
      align: 'center'
    }]
  }
  componentDidMount() {
    this._getAsyncData()
  }
  // tabskey
  _getAsyncData() {
    try {
      let ids = [],
        reqData = []
      if (hasVariant(ORDER_LIST)) {
        ids = getVariant(ORDER_LIST)
        console.log(ids)
      } else {
        removeVariant(ORDER_LIST)
      }
      if (!ids.length) return (0, this._closeWindow)(message.error(''))
      ids.map((o) => {
        reqData.push({
          externalOrderCode: o
        })
      })
      console.log(reqData)
      const {handleOutWarePrint} = this.props
      handleOutWarePrint({data: reqData})
    } catch (err) {
      console.error(err)
    }
  }
  /*  */
  _closeWindow = () => window.setTimeout(() => window.close(), 2000)
  /*  */
  handlePrint = () => {
    if (window.print) {
      window.print()
    } else {
      document.execCommand('print')
    }
    try {
      let ids = [],
        reqData = []
      if (hasVariant(ORDER_LIST)) {
        ids = getVariant(ORDER_LIST)
        console.log(ids)
      } else {
        removeVariant(ORDER_LIST)
      }
      if (!ids.length) return (0, this._closeWindow)(message.error(''))
      ids.map((o) => {
        reqData.push({
          externalOrderCode: o
        })
      })
      const {handleUpdatePrintNum} = this.props
      handleUpdatePrintNum({data: reqData})
    } catch (err) {
      console.error(err)
    }
  }

  // 
  componentDidUpdate() {
    const {printData} = this.props
    printData.map((o) => {
      JsBarcode(`#key${o.externalOrderCode}`, o.externalOrderCode, {
        width: 1.5,
        height: 30,
        displayValue: false
      })
    })
  }
  render() {
    const {printLoading, printData, tableLoading, match: {params: {id}}} = this.props
    console.log({match: {params: {id}}})
    return (
      <React.Fragment>
        {
          printData.length ? <Button className="noprint" type={'primary'} onClick={this.handlePrint}></Button> : (printLoading ? <h2 style={{textAlign: 'center'}}></h2> : <h2 style={{textAlign: 'center'}}></h2>)
        }
        {
          printData.map((o, i) => <section className="outboundOrderTemplate PageBreakAfter" key={i}>
              <div style={{position: 'relative', height: '60px', marginBottom: '20px', marginTop: '20px', borderBottom: '1px solid #000'}}>
                <h2 className="title">-{o.businessTypeDesc}</h2>
                <img id={`key${o.externalOrderCode}`} style={{position: 'absolute', top: '5px', left: '30px'}} />
                <div style={{position: 'absolute', top: '10px', right: '10px', fontSize: '12px'}}>
                  <p style={{margin: '0px'}}>{o.printTime}</p>
                  <p style={{margin: '0px'}}>{`${o.sendLocationCode}-${o.sendLocationName}`}</p>
                </div>
              </div>
              <div className='header'>
                <Row type="flex" justify="start">
                  <Col span={8} className='delBox'>
                    <label></label>
                    <span>{o.externalOrderCode}</span>
                  </Col>
                  {
                    o.businessType != '25' && <Col span={8} className='delBox'>
                    <label></label>
                    <span>{`${o.shopCode}-${o.shopName}`}</span>
                  </Col>
                  }
                  <Col span={8} className='delBox'>
                    <label></label>
                    <span>{o.businessTypeDesc}</span>
                  </Col>
                  {
                    o.businessType == '25' && <Col span={8} className='delBox'>
                    <label></label>
                    <span>{`${o.transferLocationCode || ''}-${o.transferLocationName || ''}`}</span>
                  </Col>
                  }

                  <Col span={8} className='delBox'>
                    <label></label>
                    <span>{o.productCategoryCount}</span>
                  </Col>
                  <Col span={8} className='delBox'>
                    <label></label>
                    <span>{o.orderAmount}</span>
                  </Col>
                  <Col span={8} className='delBox'>
                    <label></label>
                    <span>{o.printBy}</span>
                  </Col>
                  <Col span={8} className='delBox'>
                    <label></label>
                    <span>{o.planDate}</span>
                  </Col>
                  <Col span={8} className='delBox'>
                    <label></label>
                    <span>{o.shippedTime}</span>
                  </Col>
                </Row>
              </div>
              <div className='foot' style={{marginBottom: '10px'}}>
                <Table
                  rowKey={(record, index) => index}
                  loading={tableLoading}
                  columns={this.columns}
                  dataSource={o.shippedOrderItemList}
                  pagination={false}
                  style={{marginTop: '6px'}}
                  bordered
                  size="middle"
                />
              </div>
            </section>)
        }
        <div className="printLoadingSty">
          <Spin spinning={printLoading} size="large" />
        </div>
      </React.Fragment>
    )
  }
}



