import * as actionType from './actionType'
import {getData} from 'util'
import {API_SHIPPEDORDERS_BUSINESSPRINTPAGE_PAGE, API_SHIPPED_BUSINESSPRINTDETAIL, API_SHIPPEDORDERS_BUSINESSSHIPPEDPRINTORDERDETAIL, API_SHIPPED_BUSINESSPRINTTABS_TAB, API_SHIPPED_UPDATEBUSINESSPRINTTIMES} from 'api'
import moment from 'moment'

//
export const update = (object) => ({
  type: actionType.BUSINESSEXCEEDSLOPRINT_UPDATE,
  data: object
})

//

// 
export const handleGetList = (reqData, printFlag) => (dispatch) => {
  dispatch({type: actionType.BUSINESSEXCEEDSLOPRINT_UPDATE, data: {tableLoading: true}})
  let reqObjSearchParams = {...reqData.searchParams}
  for (let i in reqObjSearchParams) {
    if (Object.prototype.toString.call(reqObjSearchParams[i]) === '[object Object]') {
      reqObjSearchParams[i] = reqObjSearchParams[i].value
    }
  }
  if (reqObjSearchParams.initDate && reqObjSearchParams.initDate.length) {
    reqObjSearchParams.createTimeStart = moment(reqObjSearchParams.initDate[0]).format('YYYY-MM-DD')
    reqObjSearchParams.createTimeEnd = moment(reqObjSearchParams.initDate[1]).format('YYYY-MM-DD')
  }
  if (reqObjSearchParams.planDate && reqObjSearchParams.planDate.length) {
    reqObjSearchParams.planDateStart = moment(reqObjSearchParams.planDate[0]).format('YYYY-MM-DD')
    reqObjSearchParams.planDateEnd = moment(reqObjSearchParams.planDate[1]).format('YYYY-MM-DD')
  }
  delete reqObjSearchParams.initDate
  delete reqObjSearchParams.planDate
  reqData.searchParams = reqObjSearchParams

  if (printFlag) {
    getData({api: API_SHIPPEDORDERS_BUSINESSPRINTPAGE_PAGE, ...reqData}).then(data => {
      // 
      let {page, size} = reqData.searchParams
      dispatch({
        type: actionType.BUSINESSEXCEEDSLOPRINT_UPDATE,
        data: {
          page,
          size,
          tableLoading: false,
          total: data.result.total,
          selectedRowKeys: [], // key
          selectedRows: [], // 
          searchResult: data.result.list || []
        }
      })
    }).catch(err => {
      dispatch({
        type: actionType.BUSINESSEXCEEDSLOPRINT_FILE
      })
    })
  } else {
    // tabs
    getData({api: API_SHIPPED_BUSINESSPRINTTABS_TAB, ...reqData}).then(({result}) => {
      console.log(result)
      // 
      let arr = ['N', 'Y']
      let printFlag = ''
      let tabNumObj = {
        tab0: '',
        tab8: ''
      }
      result.map((o) => {
        tabNumObj[`tab${o.key}`] = o.value
        console.log(tabNumObj)
      })
      for (let i = 0; i < arr.length; i++) {
        for (let j = 0; j < result.length; j++) {
          if (result[j].key == arr[i] && result[j].value) {
            printFlag = arr[i]
            break
          }
        }
        if (printFlag) break
      }
      if (printFlag) {
        reqData.searchParams.printFlag = printFlag
      }
      getData({api: API_SHIPPEDORDERS_BUSINESSPRINTPAGE_PAGE, ...reqData}).then(data => {
        // 
        let {page, size} = reqData.searchParams
        dispatch({
          type: actionType.BUSINESSEXCEEDSLOPRINT_UPDATE,
          data: {
            page,
            size,
            tableLoading: false,
            total: data.result.total,
            tableActiveKey: printFlag,
            selectedRowKeys: [], // key
            selectedRows: [], // 
            searchResult: data.result.list || [],
            ...tabNumObj
          }
        })
      }).catch(err => {
        dispatch({
          type: actionType.BUSINESSEXCEEDSLOPRINT_FILE
        })
      })
    }).catch(err => {
      dispatch({
        type: actionType.BUSINESSEXCEEDSLOPRINT_FILE
      })
    })
  }
}

// 
export const handleOutWarePrint = (reqData) => (dispatch) => {
  dispatch({type: actionType.BUSINESSEXCEEDSLOPRINT_UPDATE, data: {printLoading: true}})
  getData({api: API_SHIPPED_BUSINESSPRINTDETAIL, ...reqData}).then(data => {
    // 
    dispatch({
      type: actionType.BUSINESSEXCEEDSLOPRINT_UPDATE,
      data: {
        printLoading: false,
        printData: data.result || []
      }
    })
  }).catch(err => {
    dispatch({
      type: actionType.BUSINESSEXCEEDSLOPRINT_FILE
    })
  })
}

// // 
export const handleRecordDetil = (reqData, callBack) => (dispatch) => {
  dispatch({type: actionType.BUSINESSEXCEEDSLOPRINT_UPDATE, data: {printLoading: true}})
  getData({api: API_SHIPPEDORDERS_BUSINESSSHIPPEDPRINTORDERDETAIL, ...reqData}).then(data => {
    console.log(data,111)
    // 
    dispatch({
      type: actionType.BUSINESSEXCEEDSLOPRINT_UPDATE,
      data: {
        modalData: data.result || [],
        printLoading: false
      }
    })
    callBack && callBack(data)
  }).catch(err => {
    dispatch({
      type: actionType.BUSINESSEXCEEDSLOPRINT_FILE
    })
  })
}

// 
export const handleUpdatePrintNum = (reqData, callBack) => (dispatch) => {
  getData({api: API_SHIPPED_UPDATEBUSINESSPRINTTIMES, ...reqData}).then(data => {
    callBack && callBack()
  }).catch(err => {
    dispatch({
      type: actionType.BUSINESSEXCEEDSLOPRINT_FILE
    })
  })
}



